.header {

    &__top {}

    &__main {
        position: relative;
        height: 52px;
        background-color: var(--white);
        box-shadow: 0 2px 4px 0 #00000012;
        position: relative;
        z-index: 99;

        @media (max-width:$desktop) {
            height: auto;
            box-shadow: none;
        }
    }

    @media (max-width:$desktop) {
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 99;
    }
}

.header-top {
    height: 40px;
    background-color: var(--primary100);
    background: rgba(235, 241, 251, 0.4);

    &__container {
        display: flex;
        align-items: center;
        height: 100%;
    }

    .header-top__city {
        @media (max-width:$desktop) {
            display: none;
        }
    }

    &__city {
        @media (max-width:$desktop) {
            margin-top: 33px;
            padding: 0 16px;

            svg {
                width: 10px;
                height: 12px;
                margin-right: 26px;
            }

            .city {
                &__name {
                    font-weight: 500;
                    font-size: 14px;
                    line-height: math.div(19, 14);
                }
            }
        }
    }

    &__actions {
        display: flex;
        align-items: center;

        margin-left: auto;
        margin-right: 70px;

        @media (max-width:$desktop) {
            display: none;
        }
    }

    &__action {
        &:not(:last-child) {
            margin-right: 40px;
        }
    }

    &__personal {
        @media (max-width:$desktop) {
            margin-left: auto;
        }
    }

    @media (max-width:$desktop) {
        background-color: var(--white);
        box-shadow: none;
        position: relative;
        z-index: 98;
        height: 50px;
    }
}

.header-action {
    &--add {
        &:hover {
            .header-action__title {
                color: var(--blue);
            }
        }
    }

    &--calendar {}

    &--chat {
        .header-action__title {
            svg {
                width: 14px;
                height: 14px;
            }
        }
    }

    &__title {
        color: var(--primaryTwo);
        display: flex;
        align-items: center;
        position: relative;

        svg {
            width: 14px;
            height: 14px;
            fill: currentColor;
        }
    }

    &__name {
        margin-left: 10px;
        font-size: 14px;
        font-weight: 500;
        line-height: math.div(19, 14);
    }

    &__quantity {
        height: 16px;
        min-width: 16px;
        padding: 0 5px;
        color: var(--blue);
        background-color: var(--white);

        backdrop-filter: blur(2px);
        border-radius: 50%;

        position: absolute;
        bottom: 8px;
        right: -10px;


        font-size: 10px;
        line-height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.header-personal {
    &__title {
        display: flex;
        align-items: center;
        color: var(--primaryTwo);
        transition: color $transitionSpeed $transitionFunction;

        svg {
            width: 14px;
            height: 14px;
            fill: currentColor;
        }

        &:hover {
            color: var(--blue);
        }
    }

    &__name {
        color: inherit;
        margin-right: 10px;
        font-size: 14px;
        line-height: math.div(19, 14);
        font-weight: 500;
    }
}

.header-main {
    &__container {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .header-main__logo {
            @media (max-width:$desktop) {
                display: none;
            }
        }
    }

    &__logo {
        @media (max-width:$desktop) {
            margin-left: 16px;
        }
    }

    &__nav {
        @media (max-width:$desktop) {
            display: flex;
            flex-direction: column;
            position: fixed;
            z-index: 999;
            top: 0;
            left: 0;
            width: 100%;
            min-height: var(--vh);
            height: 100%;
            background-color: var(--white);
            padding: 24px 0;

            overflow-y: auto;
            pointer-events: none;
            opacity: 0;
            visibility: hidden;

            transition: opacity $transitionSpeed $transitionFunction,
                visibility $transitionSpeed $transitionFunction;

            &.menu--active {
                opacity: 1;
                visibility: visible;
                pointer-events: all;
            }

            .nav__list {
                margin-top: 16px;
                height: auto;
                flex-direction: column;
                align-items: start;
                padding: 0 16px;
            }

            .nav__item {
                padding: 0;
                width: 100%;
            }

            .nav__link {
                width: 100%;
                padding: 16px 0;
                display: flex;
                align-items: center;

            }
        }
    }

    &__close {
        align-self: end;
        color: var(--blue);
        font-size: 12px;
        line-height: math.div(15, 12);
        font-weight: 500;
        padding: 0 16px;

        @media (min-width:$desktop) {
            display: none;
        }
    }

    &__title {
        margin-top: 24px;
        padding: 0 16px;
        font-size: 18px;
        line-height: math.div(23, 18);
        font-weight: 500;
        color: var(--gray);

        @media (min-width:$desktop) {
            display: none;
        }
    }

    &__banners {
        flex: 1 0 auto;
        margin-top: 70px;
        width: 100%;

        &-slide {
            height: 100% !important; // Hack! need to be redefined swiper

            &--1 {
                padding-left: 16px;
                margin-left: -16px;
            }

            &--2 {
                padding-right: 16px;
                margin-right: -16px;
            }
        }

        @media (min-width:$desktop) {
            display: none;
        }
    }
}
