.selection-estate {
    &__body {
        @media (max-width:$desktop) {
            padding: 74px 0 0 0;
            background-color: var(--primary100);
        }
    }

    &__title {
        @media (max-width:$desktop) {
            padding-left: 16px;
            padding-right: 16px;
        }
    }

    &__filter {
        margin-top: 32px;

        .filter {
            &__btn {
                height: 38px;

                @media (max-width:$desktop) {
                    margin: 0 16px;
                    width: calc(100% - 32px);
                }
            }

            &__clear {
                padding: 0 35px;
                max-width: 224px;
                width: 100%;

                @media (max-width:$desktop) {
                    max-width: none;
                    width: 50%;
                    padding: 0 22px;
                }
            }

            &__go {
                padding: 0 35px;
                max-width: 224px;
                width: 100%;

                @media (max-width:$desktop) {
                    max-width: none;
                    padding: 0 22px;
                }
            }
        }

        @media (max-width:$desktop) {
            margin-top: 24px;
            margin-bottom: 24px;
        }
    }

    &__select {

        @media (max-width:$desktop) {
            .choices {
                padding: 22.5px 16px 11.5px;
            }
        }
    }

    &__input {

        .input-primary {
            &__span {
                color: var(--primaryTwo);
            }

            &__input {
                @media (max-width:$desktop) {

                    &.active,
                    &:focus {
                        padding: 19px 16px 12px;

                        &+.input-primary__span {
                            top: 10px;
                        }
                    }
                }
            }
        }

        @media (max-width:$desktop) {
            height: 54px;
        }
    }

    &__controls {
        margin-top: 24px;
    }

    &__table {
        margin-top: 32px;

        .main-table {
            &__type {
                grid-template-columns: percent(324, 968) percent(280, 968) 1fr;

                span {
                    &:not(:first-child) {
                        text-align: center;
                        margin: 0;
                    }
                }

                @media (max-width:$desktop) {
                    display: grid !important; // need hack :( 
                }

                @media (max-width:$smallTablet-max) {
                    display: none !important; // need hack :((( 
                }
            }

            &__next {
                @media (max-width:$desktop) {
                    margin: 24px 16px 0;
                    width: calc(100% - 32px);
                }
            }
        }

        @media (max-width:$desktop) {
            padding: 0 0 16px 0;
        }

        @media (max-width:$smallTablet-max) {
            margin-top: 16px;
        }
    }

    &__banners {
        margin-top: 40px;
    }

    @media (max-width:$desktop) {
        padding-top: 0;
    }
}

.selection-estate-filter {
    &__row--1 {
        grid-template-columns: percent(230, 1080) 1fr;
    }
}
